.calculator {
  width: 300px;
  height: 600px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  box-shadow: 0 0 10px #ccc;
}

.display {
  height: 80px;
  padding-top: 10px;
}

.display input {
  width: 90%;
  height: 60px;
  text-align: right;
  background-color: #f9f9f9;
  border: none;
  border-radius: 5px;
  padding-right: 10px;
  font-size: 32px;
  box-shadow: inset 0 0 5px #ccc;
}

.buttons {
  grid-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  padding: 20px;
  display: grid;
}

button {
  width: 60px;
  height: 60px;
  color: #333;
  cursor: pointer;
  background-color: #f9f9f9;
  border: none;
  border-radius: 5px;
  font-size: 24px;
  box-shadow: 0 0 5px #ccc;
}

button:hover {
  background-color: #ddd;
}

#clear {
  grid-column: 1 / 3;
}

#backspace {
  grid-column: 3 / 4;
}

#divide, #multiply, #subtract, #add {
  color: #fff;
  background-color: #ccc;
}

#equals {
  color: #fff;
  background-color: #07f;
  grid-column: 3 / 5;
}

#decimal {
  font-size: 32px;
}

